.showcase-container {

    height: 100vh;
    width: 100%;
    background-image: url('/src/assests/bg.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlays{
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.5);
    position: absolute;
    top: 0;
}

.showcase-content{
    width: fit-content;
    z-index: 1;
}

.showcase-content h1 {
    color: white;
    text-align: center;
    padding-bottom: 10px;
    font-family: "Chivo Mono";
}
.showcase-content p{
    color: white;
    text-align: center;
    padding-bottom: 10px;
    font-family: "Chivo Mono";
    
}

.showcase-content span{
    font-family: "chivo mono";
}