.product-listing-card{
    border: 1px solid #aaa;
    width: 100%;
    padding: 2rem 1rem;
    border-radius: 10px;
}
.product-listing-img-container{
    width: 100%;
    height: 15rem;
    text-align: center;
}

.product-listing{

    width: 100%;
    height: 100%;
    object-fit: contain;

}

.product-listing-details-container{

    padding-top: 2rem;
    min-height: 10rem;
}

.author-name{
    font-size: 0.9rem;
    padding: 0.5rem 0;
    font-family: "Chivo Mono";
    
}

.pricing{
    font-family: "Chivo Mono";
    font-size: 1.5rem;
    font-weight: 700;
    color: var(--primary-color-bright);


}

.product-listing-button{
    display: inline-block;
    background-color: var(--primary-color-bright);
    width: 100%;
    text-decoration: none;
    color: white;
    padding: 1rem 0;
    text-align: center;
    font-family: "Chivo Mono";    
    border-radius: 3px;
    margin-top: 2rem;

}

.product-listing-button:hover{
    background-color: black;
    transition: 0.2s ease-in;
   
}