.footer-container{
    background-color: #222;
    padding: 5rem 0;
}

.footer-container p{
    color: var(--white-color);
    padding-top: 2rem;
    font-size: 0.8

}

.footer-container h2{
    color: var( --white-color);
}

.footer-form{
    padding-top: 3rem;
    width: 50%;
    color:var( --white-color);
}

.form-group{
    padding: 0.5rem 0;

}

.form-input{
    width: 100%;
    padding: 1rem;
    border-radius: 5px;
    font-size: 1rem;
    outline: none;

}

.form-lable{
    display: inline-block;
    color: var( --white-color);
    padding: 5px 0;
}
.form-submit{
    display: inline-block;
    text-decoration: none;
    color: var( --white-color);
    border: 1px solid black;
    border-radius: 4px;
    font-family: var(--font-family);
    padding: 0.5rem 1.5rem;
    text-align: center;
    font-size: 1rem;
    background-color: var(--primary-color-bright);
    cursor: pointer;
}


.form-submit:hover{
    background-color: var(--white-color);
    color: #333;
    transition: 0.3s ease-in;
}