.signup-container{
    display: flex;
    justify-content: space-between;
    align-items: center;

}


.signup-img-containetr{
    width: 45%;
    overflow: hidden;

}

.signup-img-containetr img{
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.signup-content-container{
    width: 50%;
    
}
.content-wrapper{
    padding-right: 3rem;
}

.content-wrapper h2{
    padding-bottom: 1rem;
    font-size: 2rem;
    font-family: "Chivo Mono";
}
.content-wrapper p{
    font-family: "Chivo Mono";
}

.button-primary{
    cursor: pointer;
}