.deatil-section-container{
    padding-top: 8rem;
    padding-bottom: 8rem;
}

.flex-container{
    display: flex;
    justify-content: space-between;
}

.book-image-container, .book-detail-container{
    width: 50%;

}

.book-image-container{
    text-align: center;
}

.book-image-container img{
    height: 400px ;
    border: 1px solid #ddd;
}

.book-detail-container{
    font-size: 1rem;
}

.book-detail-container p{
    padding: 1rem 0;
}

.book-description{
    line-height: 1.5rem;
}

.book-detail-container h3{
    font-size: 1.5rem;
    padding-top: 1rem;
    color: var(--primary-color-bright);
}

