.navbar-container{

    position: absolute;
    top: 0;
    width: 100%;
    z-index: 1;
    padding: 1rem 0px;
}

.logo{
    font-size: 1.5rem;
    color: white;
    text-decoration: none;
    font-family: "Chivo Mono";
    font-weight: 700;
}
.logo span{
    font-family: "Chivo Mono";
}

.nav-links, .nav-links-dark{
    text-decoration: none;
    color: white;
    padding-left: 10px;
    font-size: 1.2rem;
    transition: 0.2s ease-in;
}

.nav-links-dark{
    color: #333;
}

.nav-links-dark:hover{
    color: var(--primary-color) ;

}

.nav-links-container{
    display: flex;
    align-items: center;
    cursor: pointer;
}



.cart-link{
    display: inline-block;
    padding-left: 20px ;
    cursor: pointer;
}

.nav-links:hover{
    color: var(--primary-color) ;
    ;
}

.background-dark{
    background-color: #333;
}

.background-transparent{
    background-color: transparent;
}

.relative{
    position: relative !important;
}