.search-input-form-container{
    display: flex;
    padding: 1rem;
    background: white;
    border-radius: 5px;
    margin-top: 20px;
    

}
.search-input{
    flex: 1;
    outline: none;
    border: none;
    font-size: 1.2rem;
    padding-left: 10px;
}

.search-button{
    padding: 0.5rem 1.2rem;
    border: none;
    background: var(--primary-color);
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s ease-in;;

}
.search-button:hover{
    background: var(--primary-color-bright);
}

.dark-box-shadow{
    box-shadow: 4px 4px 15px #111;
}

.light-box-shadow{
    box-shadow: 4px 4px 15px #ccc;
}