
.cart-item{
    padding: 2rem;
    border: 1px solid black;
    display: flex;
    border-radius: 1rem;
    margin-bottom: 1rem;

}
.cart-item-image{
    width: 6rem;
}

.cart-item-content-container{
    margin: 0.5rem 0rem;
    padding-left: 2rem;
    padding-top: 10px;
}

.cart-item-content-container p{
    padding-top: 10px;
}

.cart-item-price{
    padding-top: 8px;
    color: var(--primary-color-bright);
}

.dlt-btn{
    background-color: #333;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    margin-top: 1rem;
    cursor: pointer;
    transition: 0.3s ease-in;
    font-family: var( --font-family);
}

.dlt-btn:hover{

    background-color: #555;
    
}